html,body {
  height: 100%;
  margin: 0px;
  padding: 0px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Ubuntu",  "Segoe UI", "Roboto", "Oxygen",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.pull-right {
  float: right;
}

.full-page {
  height: 100%;
}

.black-dot {
  border-radius: 50%;
  background: #3B3B3B;
  color: white;
  padding: 3px 2px 2px 2px;
  font-size: 12px;
  font-weight: bold;
}



.App {
  text-align: center;
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
}



.MapContainer{
      width: 100%;
}

.MapContainer__map {
      height: 100%;
}

.MapContainer__error {
      padding: 8px;
      color: red;
      font-size: 17px;
      position: fixed;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
}

.MapContainer__loader {
      position: absolute;
      left: 50%;
      top: 50%;
      height: 30px;
      width: 30px;
      opacity: 0;
      margin: 0 0 0 -150px;
      border: 5px solid #333;
      border-radius: 30px;
      -webkit-animation: pulsate 1s ease-out;
              animation: pulsate 1s ease-out;
      -webkit-animation-iteration-count: infinite;
              animation-iteration-count: infinite;
}

  @-webkit-keyframes pulsate {
      0% {
            -webkit-transform: scale(.1);
                    transform: scale(.1);
            opacity: 0.0;
      }
      50% {
            opacity: 1;
      }
      100% {
            -webkit-transform: scale(1.2);
                    transform: scale(1.2);
            opacity: 0;
      }
}

  @keyframes pulsate {
      0% {
            -webkit-transform: scale(.1);
                    transform: scale(.1);
            opacity: 0.0;
      }
      50% {
            opacity: 1;
      }
      100% {
            -webkit-transform: scale(1.2);
                    transform: scale(1.2);
            opacity: 0;
      }
}


.WaypointPanel {
      text-align:left;
      margin: 0;
      background: #3B3B3B;
      width: 300px;
      color: white;
}


.WaypointList  {
      width: 100%;
      height: 100%;
}

.WaypointList .guidelines {
      padding: 0 20px;
      line-height: 1.5em;
      color: #AAA;
}

.WaypointItem {
      background: inherit;
      border: 0;
      font-size: 17px;
      padding: 7px 15px;
}

.WaypointItem.draggedOn {
      border-left: 2px solid white;
      border-right: 2px solid white;
      padding: 7px 13px;
      background: #555;
}

.WaypointItem i {
      font-size: 14px;
      color: #CCC;
      margin-right: 19px;
      cursor: pointer;
}
.WaypointPanelHeader {
      margin: 15px;
      padding-bottom: 10px;
      border-bottom: 1px solid #AAA;
}

.WaypointPanelHeader .download-btn {
      background: #757575;
      margin-right: 10px;
      padding: 8px;
      border-radius: 50%;
      float: right;
      font-size: 12px;
      position: relative;
      top: -8px;
}

.WaypointPanelHeader a {
      color: #EFEFEF;
}
